import styles from './faculty.module.css'
import nav from '../navigation.module.css'
import React from 'react';
import { useForm, SubmitHandler } from "react-hook-form";
import {useState} from 'react';
import {BrowserRouter as Routes, Route, Outlet, Link} from 'react-router-dom'
declare type FormValues = {
    fullName: string;
    email: string;
    phone: string;
    subject: string;
    message: string;
    file: FileList;
  };
interface navOption {
    name: string,
    link: string
}
const importAll = (requireContext: __WebpackModuleApi.RequireContext): string[] => 
    requireContext.keys().map((key) => requireContext(key).default || requireContext(key));
export const Contacts = () => {
  
    const { register, handleSubmit, formState: { errors }, reset } = useForm<FormValues>();
    const images = importAll(require.context('./images', false, /\.(png|jpe?g|svg)$/));
    const scrollToForm = () => {
        document.getElementById("form")?.scrollIntoView({ behavior: "smooth" });
    }
    const handleEmail: SubmitHandler<FormValues> = async (data) => {
        console.log(" :", data); 
        const emailContent = `
        <div style="width: 1024px; height: fit-content;">
  <div style="height: 80px; background-color: black; display: flex">
    <img 
      src="https://it.belstu.by/wp-content/uploads/2019/05/LogoFIT-1-e1557999163520.png"
      style="height: 60px; margin: 10px"/>
    <h1 style="color: white; line-height: 55px; margin: 10px">   </h1>
  </div>
  <div style="padding: 10px; font-size: 18px; background-color: #262121" color: white !important>
      <p style="color: white">, ${data.fullName}!     .  ,     .   .</p>
      <p style="font-size: 14px; color: white;"> ,<br>    .</p>
  </div>
</div>
`
        try {
          const response = await fetch('http://localhost:5000/send-email', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              email: data.email,
              subject: "   ",
              html: emailContent
            }),
          });
      
          if (!response.ok) {
            throw new Error('Failed to send email');
          }
      
          const result = await response.json();
          alert(result.message);
        } catch (error) {
          console.error('Error in handleEmail:', error);
          alert('   ');
        }
      };
      
      
    const onSubmit: SubmitHandler<FormValues> = (data) => {
        console.log(data);
        handleEmail(data);
        reset();
    };
    return (
        <div className={styles.contacts}>
            <div className={styles.persons}>
            <div className={styles.person}>
                <img src={images[1]}/>
                <div className={styles.desc}>
                    <h1>  </h1>
                    <h2> </h2>
                    <p>  , </p>
                    <p>. 104  4</p>
                    <p>. <a href="tel:80173993389">8 (017) 399-3389</a></p>
                    <p> <a href="mailto:it@belstu.by">it@belstu.by</a></p>
                    <p>:   13.00  16.00</p>
                    <p> <a onClick={scrollToForm}> </a></p>
                </div>
            </div>
            <div className={styles.person}>
                <img src={images[0]}/>
                <div className={styles.desc}>
                    <h1>  </h1>
                    <h2>.    <br/>  </h2>
                    <p>   </p>
                    <p>. 104  4</p>
                </div>
            </div>
            <div className={styles.person + ' ' + styles.blank}>
                <img src={images[2]}/>
                <div className={styles.desc}>
                    <h1>  </h1>
                    <h2></h2>
                    <p> :</p>
                    <p>-: 8:30 - 16:15, 12:00 - 12:45</p>
                    <p>: 8:30 - 13:30</p>
                </div>
            </div>
            <div className={styles.person + ' ' + styles.blank}>
                <img src={images[2]}/>
                <div className={styles.desc}>
                    <h1>  </h1>
                    <h2></h2>
                    <p> :</p>
                    <p>-: 8:30 - 16:15, 12:00 - 12:45</p>
                    <p>: 8:30 - 13:30</p>
                </div>
            </div>
        </div>
        <div className={styles.formblock}>
        <form className={styles.form} onSubmit={handleSubmit(onSubmit)} id="form">
      <label className={styles.label}> </label>
      
      <input 
        {...register("fullName", { required: "  " })} 
        className={styles.input} 
        placeholder=" " 
      />
      {errors.fullName && <p className={styles.error}>{errors.fullName.message}</p>}
      
      <input 
        {...register("email", { 
          required: "  email", 
          pattern: { 
            value: /^\S+@\S+$/i, 
            message: " email"
          }
        })} 
        className={styles.input} 
        placeholder=" email" 
      />
      {errors.email && <p className={styles.error}>{errors.email.message}</p>}
      
      <input 
        {...register("phone", { 
          pattern: { 
            value: /^\+?\d{10,15}$/, 
            message: " "
          }
        })} 
        className={styles.input} 
        placeholder=" " 
      />
      
      <input 
        {...register("subject")} 
        className={styles.input} 
        placeholder="" 
      />
      
      <textarea 
        {...register("message", { required: " " })} 
        className={`${styles.input} ${styles.textarea}`} 
        placeholder="" 
      />
      {errors.message && <p className={styles.error}>{errors.message.message}</p>}
      
      <input 
        {...register("file")} 
        className={`${styles.input} ${styles.file}`} 
        type="file" 
      />
      <button 
        type="submit" 
        className={`${styles.input} ${styles.button}`}>
        
      </button>
    </form>
        </div>
        </div>
    )
}
export const Faculty = () => {
  const [activeButton, setActiveButton] = useState<number>(1);
  const navOptions: navOption[] = [
      { name: " ", link: "about" },
      { name: "", link: "contacts" },
      { name: " ", link: "eduwork" },
      { name: "", link: "scolarship" },
      { name: "", link: "dormitory" },
  ];

  return (
      <div className={styles.faculty}>
          <p className={nav.header}></p>
          <div className={nav.navigation}>
              {navOptions.map((elem, index) => (
                  <Link
                      key={index}
                      to={elem.link} 
                      className={`${nav.navbutton} ${activeButton === index ? nav.show : ''}`}
                      onClick={() => setActiveButton(index)}
                  >
                      {elem.name}
                  </Link>
              ))}
          </div>
          <div className={styles.content}>
              <Outlet /> 
          </div>
      </div>
  );
};